<template>
  <div class="calssDialogBox">
    <el-dialog
      :title="dialogTitle"
      :visible="dialogFormVisible"
      v-if="dialogFormVisible"
      @close="classDialogClose"
    >
      <el-form :model="classForm" :rules="rules" ref="classForm">
        <el-form-item label="班级代号" :label-width="formLabelWidth" prop="classNo">
          <el-input v-model="classForm.classNo" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="班级名称" :label-width="formLabelWidth" prop="className">
          <el-input v-model="classForm.className" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="专业代号" :label-width="formLabelWidth" prop="professionNo">
          <el-input v-model="classForm.professionNo" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="专业名称" :label-width="formLabelWidth" prop="professionName">
          <el-input v-model="classForm.professionName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="班级人数" :label-width="formLabelWidth" prop="classNum">
          <el-input v-model="classForm.classNum" autocomplete="off"></el-input>
        </el-form-item>
        <!-- <el-form-item label="就业人数" :label-width="formLabelWidth" prop="employNum">
          <el-input v-model="classForm.employNum" autocomplete="off" disabled value=0></el-input>
        </el-form-item>-->
        <el-form-item label="班长" :label-width="formLabelWidth" prop="monitor">
          <el-input v-model="classForm.monitor" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="班长联系方式" :label-width="formLabelWidth" prop="monitorTel">
          <el-input v-model="classForm.monitorTel" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelEdit">取 消</el-button>
        <el-button type="primary" @click="submitForm('classForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { isPhone } from "./../../../../../utils/utils";
import { addNewClass, updateClass } from "../../../../../utils/employment.url";
export default {
  props: ["dialogFormVisible", "classForm", "dialogTitle"],
  data() {
    return {
      formLabelWidth: "120px",
      rules: {
        classNo: [
          { required: true, message: "请输入班级代号", trigger: "blur" },
          { min: 7, max: 7, message: "输入7位班级代号", trigger: "blur" }
        ],
        className: [
          { required: true, message: "请输入班级名称", trigger: "blur" }
        ],
        professionNo: [
          { required: true, message: "请输入专业代号", trigger: "blur" }
        ],
        professionName: [
          { required: true, message: "请输入专业名称", trigger: "blur" }
        ],
        classNum: [
          { required: true, message: "请输入班级人数", trigger: "blur" }
        ],
        monitor: [
          { required: true, message: "请输入班长姓名", trigger: "blur" }
        ],
        monitorTel: [{ required: true, validator: isPhone, trigger: "blur" }]
      }
    };
  },
  methods: {
    //关闭
    classDialogClose() {
      this.$refs["classForm"].resetFields();
      this.$emit("changeDialogVisible", false);
    },
    //,取消
    cancelEdit() {
      // this.dialogFormVisible = false;
      this.$emit("changeDialogVisible", false);
    },
    //submitForm
    submitForm(formName) {
      if (this.dialogTitle == "修改班级信息") {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.updateClass();
          } else {
            return false;
          }
        });
      } else {
        this.addNewClass();
      }
    },
    //添加新班级
    addNewClass() {
      this.classForm.employNum = 0;
      addNewClass(this.classForm, "post")
        .then(res => {
          if (res.status == 200) {
            this.$emit("changeDialogVisible", false);
            this.$message({
              message: res.message,
              type: "success"
            });
          } else if (res.status == 401) {
            this.$message({
              message: res.message,
              type: "warning"
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //修改新班级
    updateClass() {
      this.$emit("changeDialogVisible", false);
      updateClass(this.classForm, "put").then(res => {
        if (res.status == 200) {
          this.$emit("updateClass");
          this.$message({
            message: res.message,
            type: "success"
          });
        }
      });
    },
    beforeClose() {
      this.dialogFormVisible = false;
      this.$emit("changeDialogVisible", this.dialogFormVisible);
    }
  },
  created() {}
  // watch:{
  //   dialogFormVisible(value){
  //     this.$emit('changeDialogVisible',value)
  //   }
  // }
};
</script>
<style lang="scss" scoped>
</style>